<script>
import { mapGetters } from 'vuex';

export default {
  name: 'EditCertificateModal',
  computed: {
    ...mapGetters(['CertificateTypes', 'loginUser']),
    courseID() {
      return this.courseid;
    },
  },
  mounted() {
    this.$store.dispatch('getCertificatesTypes');
  },
  props: ['courseid', 'certData'],
  watch: {
    certData() {
      this.showModal = true;
      this.resetFromError();
      this.formData = {
        ...this.certData,
        fk_course_id: this.courseID,
        account_email: this.certData.Account.email,
        type: this.certData.Certificate.Type,
        issued: this.$moment(this.certData.Certificate.Issued).format('YYYY-MM-DD'),
        activated: this.certData.Certificate.Activated,
        kin: this.certData.Certificate.Kin,
        passport: this.certData.Certificate.Passport,
        rank: this.certData.Certificate.Rank,
        date_of_birth: this.$moment(this.certData.Certificate.DateOfBirth).format('YYYY-MM-DD'),
        licence: this.certData.Certificate.Licence,
        ship_experience: this.certData.Certificate.ShipExpereince,
        experience: this.certData.Certificate.Experience,
        nationality: this.certData.Certificate.Nationality,
        verified: this.certData.Certificate.Verified,
        account_name: this.certData.Account.name,
        completed: this.certData.Certificate.Completed,
      };
    },
  },
  data() {
    return {
      showModal: false,
      formData: {
        fk_course_id: this.courseID,
        account_email: '',
        type: 1,
        issued: this.$moment().format('YYYY-MM-DD'),
        activated: true,
        kin: '',
        passport: '',
        rank: '',
        date_of_birth: this.$moment().format('YYYY-MM-DD'),
        licence: '',
        ship_experience: '',
        experience: '',
        nationality: '',
        verified: false,
        course_id: '',
        account_name: '',
      },
      error: {
        fk_course_id: null,
        account_email: '',
        type: '',
        issued: '',
        activated: null,
        kin: '',
        passport: '',
        rank: '',
        date_of_birth: '',
        licence: '',
        ship_experience: '',
        experience: '',
        nationality: '',
        verified: null,
        course_id: '',
        account_name: '',
      },
      submiting: false,
      formMode: 'create',
    };
  },
  methods: {
    async editSubmit() {
      this.submiting = true;
      const tempFormData = {
        ...this.formData,
        issued: this.$moment(this.formData.issued).toISOString(true),
        date_of_birth: this.$moment(this.formData.date_of_birth).toISOString(true),
        fk_course_id: this.courseID,
        id: this.certData.Certificate.ID,
      };
      const response = await this.$store.dispatch('editCertificate', {
        payload: tempFormData,
        resetform: this.resetFullFormandCloseModal,
      });
      if (response.formError) {
        this.error = response.formError;
      }
      this.submiting = false;
    },
    // setEditData(item) {
    //   this.resetFromError();
    //   this.formData = { ...this.formData, ...item };
    //   this.formMode = "edit";
    // },
    resetform() {
      this.submiting = false;
      this.formData = {
        fk_course_id: this.courseID,
        account_email: '',
        type: 1,
        issued: this.$moment().format('YYYY-MM-DD'),
        activated: true,
        kin: '',
        passport: '',
        rank: '',
        date_of_birth: this.$moment().format('YYYY-MM-DD'),
        licence: '',
        ship_experience: '',
        experience: '',
        nationality: '',
        verified: false,
        course_id: '',
        account_name: '',
      };
    },
    resetFullFormandCloseModal() {
      this.resetform();
      this.resetFromError();
    },
    resetFromError() {
      this.error = {
        fk_course_id: '',
        account_email: '',
        type: '',
        issued: '',
        activated: null,
        kin: '',
        passport: '',
        rank: '',
        date_of_birth: '',
        licence: '',
        ship_experience: '',
        experience: '',
        nationality: '',
        verified: '',
        course_id: '',
        account_name: '',
      };
    },
  },
};
</script>

