import Vue from 'vue';
import store from '../store/store';

class AuthService {
  constructor() {
    this.isAuthenticated = this.isAuthenticated.bind(this);
    this.authenticated = this.isAuthenticated();
  }

  // eslint-disable-next-line class-methods-use-this
  async isAuthenticated() {
    // let expiresAtValue = localStorage.getItem('expires_at')
    const cookieToken = Vue.$cookies.get('access_token');
    // const now = new Date();
    // if(store.state.auth.user === null){
    //   if (!expiresAtValue || expiresAtValue == 'NaN') {
    //     localStorage.removeItem('expires_at')
    //     localStorage.removeItem('user')
    //     return false
    //   }
    // }
    // let expiresAt = Date(cookieToken.refreshed_at);
    if (!cookieToken) {
      return false;
    }
    if (store.state.auth.user) {
      return true;
    }
    if (cookieToken && cookieToken.token && store.state.auth.user === null) {
      await store.dispatch('getUserProfile', null);
      return true;
    }
    return false;
  }
}

export default AuthService;
