<template>
  <div class="uk-container">
    <div style="padding-top: 50px"></div>

    <article class="uk-article">
      <h1 class="uk-heading-divider">Accounts</h1>
      <div class="uk-margin-auto">
        <div class="uk-margin-auto" v-if="usersLoading" uk-spinner></div>
      </div>
      <div v-if="!usersLoading">
        <!-- <table class="uk-table uk-table-striped uk-table-hover uk-text-left "> -->
        <table
          class="
            uk-table uk-table-small uk-table-striped uk-table-hover uk-text-left
          "
        >
          <thead>
            <tr>
              <th>Id</th>
              <th>Account Name</th>
              <th>Email Verified</th>
              <th>Status</th>
              <th>Role</th>
              <th class="m-auto"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in usersData" :key="index">
              <td>{{ item.id }}</td>

              <router-link :to="`/user/${item.id}/course`">
                <td>{{ item.name }}</td>
              </router-link>
              <td>{{ item.email_validated ? "Yes" : "No" }}</td>
              <td>{{ rowStatus(item.status) }}</td>
              <td>{{ rowRole(item.role) }}</td>
              <td class="uk-flex">
                <p
                  class="cursor_pointer m-auto"
                  @click="setEditData(item)"
                  uk-toggle="target: #users-edit-modal"
                >
                  <button class="uk-button uk-button-default uk-button-small">
                    <i class="fas fa-edit"></i>
                  </button>
                </p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- This is a button toggling the modal -->

      <!-- This is the modal -->
      <div id="user-edit-modal-container">
        <div
          id="users-edit-modal"
          uk-modal
          container="#user-edit-modal-container"
        >
          <div class="uk-modal-dialog uk-modal-body uk-box-shadow-large">
            <div class="uk-grid-margin uk-grid uk-grid-stack" uk-grid>
              <div class="uk-width-1-1@m">
                <h3 class="uk-card-title uk-text-center uk-margin-small">
                  Edit
                </h3>
                <form class="uk-width-1-1" @submit.prevent="createSubmit">
                  <div class="uk-margin">
                    <label class="uk-form-label"
                      >User<span class="uk-text-danger"><b>*</b></span></label
                    >
                    <div class="uk-inline uk-width-1-1">
                      <input
                        class="uk-input uk-form-large"
                        placeholder="Name"
                        type="text"
                        v-model="formData.name"
                        disabled
                      />
                    </div>
                    <div class="uk-text-danger">{{ error.name }}</div>
                  </div>
                  <div class="uk-margin">
                    <label class="uk-form-label"
                      >Status<span class="uk-text-danger"><b>*</b></span></label
                    >
                    <div class="uk-inline uk-width-1-1">
                      <select class="uk-select" v-model="formData.status">
                        <option
                          v-for="(item, index) in statusTypes"
                          :key="index"
                          :value="item.id"
                        >
                          {{ item.name }}
                        </option>
                      </select>
                    </div>
                    <div class="uk-text-danger">{{ error.status }}</div>
                  </div>
                  <div class="uk-margin">
                    <label class="uk-form-label"
                      >Role<span class="uk-text-danger"><b>*</b></span></label
                    >
                    <div class="uk-inline uk-width-1-1">
                      <select class="uk-select" v-model="formData.role">
                        <option
                          v-for="(item, index) in roleTypes"
                          :key="index"
                          :value="item.id"
                        >
                          {{ item.name }}
                        </option>
                      </select>
                    </div>
                    <div class="uk-text-danger">{{ error.role }}</div>
                  </div>
                  <div class="uk-margin">
                    <button
                      class="
                        uk-button uk-button-primary uk-button-large uk-width-1-1
                      "
                      :disabled="submiting"
                    >
                      Submit
                      <div
                        class="uk-margin-left"
                        v-if="submiting"
                        uk-spinner
                      ></div>
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </article>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'UsersListing',
  computed: {
    ...mapGetters(['usersData', 'usersLoading', 'roleTypes', 'statusTypes']),
  },
  mounted() {
    this.$store.dispatch('getUsers');
    this.$store.dispatch('getUsersRoleTypes');
    this.$store.dispatch('getUsersStatusTypes');
  },
  data() {
    return {
      formData: {
        email: null,
        email_validated: null,
        id: null,
        name: null,
        role: null,
        status: null,
      },
      error: {
        email: null,
        email_validated: null,
        id: null,
        name: null,
        role: null,
        status: null,
      },
      submiting: false,
      roleType: {
        1: 'Unverified',
        2: 'User',
        3: 'Certifier',
        4: 'Admin',
      },
      statusType: {
        1: 'Active',
        2: 'Blocked',
      },
    };
  },
  methods: {
    async createSubmit() {
      this.submiting = true;
      const response = await this.$store.dispatch('editUser', this.formData);
      if (response.formError) {
        this.error = response.formError;
      }
      this.submiting = false;
    },
    setEditData(item) {
      this.resetFromError();
      this.formData = { ...this.formData, ...item };
    },
    resetFromError() {
      this.error = {
        email: null,
        email_validated: null,
        id: null,
        name: null,
        role: null,
        status: null,
      };
    },
    rowRole(value) {
      const name = this.roleType[value];
      return name;
    },
    rowStatus(value) {
      const name = this.statusType[value];
      return name;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.cursor_pointer {
  cursor: pointer;
}
.m-auto {
  margin: auto;
}
</style>
