<template>
  <div class="uk-container">

    <div style="padding-top: 50px"></div>

    <article class="uk-article" v-if="!isLoading">
      <ul class="uk-breadcrumb">

        <li>
          <a href="/courses"
            ><h3>{{ Certificatedata.Course.course_name }}</h3></a
          >
        </li>
        <li class="uk-disabled">
          <a href="#"
            ><h3>{{ Certificatedata.Course.certificate_name }}</h3></a>&nbsp;

        </li>
      </ul>


      <div>
        <div class="uk-margin-auto " v-if="isLoading" uk-spinner></div>
        <button
          class="uk-button uk-button-default uk-button-small uk-button-primary"
          uk-toggle="target: #certificate-modal"
          type="button"
          v-if="loginUser.role === 3 || loginUser.role === 4">
          <i class="fas fa-plus"></i> Create new certificate
        </button>

      </div>

      <table class="uk-table uk-table-divider">
        <tbody>

          <tr>
            <td>Category</td>
            <td>{{ Certificatedata.Course.name }}</td>
          </tr>
          <tr>
            <td>Created</td>
            <td>{{  $moment(Certificatedata.Course.created).format(
                    "DD-MM-YYYY"
                  ) }}</td>
          </tr>
          <tr>
            <td>Course Expire in months</td>
            <td>{{ Certificatedata.Course.expire }}</td>
          </tr>
          <!-- <tr>
            <td>Course Country</td>
            <td>{{ Certificatedata.Course.country }}</td>
          </tr>
          <tr>
            <td>Course Description</td>
            <td>{{ Certificatedata.Course.description }}</td>
          </tr>
          <tr>
            <td>Course Additional Description</td>
            <td>{{ Certificatedata.Course.additional_description }}</td>
          </tr> -->
        </tbody>
      </table>

      <!-- <div class="uk-overflow-auto uk-card-body uk-box-shadow-large"> -->
      <table
        class="uk-table uk-table-small uk-table-striped uk-table-hover"
        v-if="!isLoading && Certificatedata.AccountCertificates"
      >
        <thead>
          <tr>
            <th>Acount Email</th>
            <th>Issued</th>
            <th>Completed</th>
            <th>Verified</th>
            <th class="m-auto"></th>

            <!-- <th>Issued</th>
            <th>Nationality</th>
            <th>Licence</th>
            <th>Rank</th>
            <th>activated</th>
            <th class="m-auto"></th> -->
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in Certificatedata.AccountCertificates" :key="index">
            <td>
              {{ item.Account.email }}
            </td>

            <td>
               {{
                  $moment(item.Certificate.Issued).format(
                    "DD-MM-YYYY"
                  )
                }}
            </td>
            <!-- <td>
                {{
                $moment(item.Certificate.Issued)
                  .add(item.Course.Expire, "M")
                  .format("DD-MM-YYYY")
              }}
            </td> -->
            <td>
               <i class="fas fa-circle" v-bind:style="{ 'color': [item.Certificate.Completed ? 'green' : 'red'] }"  ></i>
            </td>
            <td>
               <i class="fas fa-circle" v-bind:style="{ 'color': [item.Certificate.Verified ? 'green' : 'red'] }"  ></i>
            </td>
            <td class="uk-flex">
              <div class="cursor_pointer m-0">
                <a
                  class="uk-button uk-button-default uk-button-small"
                  target="_blanck"
                  :href="`/view/certificate/${item.Certificate.UID}`"
                >
                  <i class="fas fa-eye"></i>
                </a>
              </div>
              <div
                class="cursor_pointer m-0"
                @click="setEditData(item)"
                uk-toggle="target: #certificate-edit-modal"
              >
                <button class="uk-button uk-button-default uk-button-small">
                  <i class="fas fa-edit"></i>
                </button>
              </div>
              <div class="cursor_pointer m-0" @click="deleteCertificate(item)">
                <button class="uk-button uk-button-default uk-button-small">
                  <i class="fas fa-trash"></i>
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <div
        v-if="!isLoading && !Certificatedata.AccountCertificates"
        class="uk-text-center"
      >
        <h1 class="uk-text-meta uk-text-large">No Certificate Data</h1>
      </div>
      <CreateCertificateModal :courseid="$route.params.id" />
      <EditCertificateModal
        :certData="selectedEditData"
        :courseid="$route.params.id"
      />
    </article>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import CreateCertificateModal from './CreateCertificateModal.vue';
import EditCertificateModal from './EditCertificateModal.vue';

export default {
  name: 'CertificatesListing',
  components: {
    CreateCertificateModal,
    EditCertificateModal,
  },
  computed: {
    ...mapGetters([
      'Certificatedata',
      'isLoading',
      'CertificateTypes',
      'loginUser',
    ]),
  },
  mounted() {
    this.$store.dispatch('getCertificatesApi', this.$route.params.id);
    this.$store.dispatch('getCertificatesTypes');
  },
  data() {
    return {
      selectedEditData: null,
    };
  },
  methods: {
    setEditData(item) {
      this.selectedEditData = item;
      // document.getElementById("certificate-edit-modal")
    },
    deleteCertificate(item) {
      this.$swal({
        title: 'Delete this course?',
        text: 'This action cannot be undone, please confirm to continue',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        confirmButtonText: 'Confirm',
        item,
      }).then((result) => {
        if (result.value) {
          this.$store.dispatch('deleteCertificate', {
            ...item,
            courseId: this.$route.params.id,
          });
        }
      });
    },
  },
};
</script>

