<template>
  <div class="uk-section uk-section-muted uk-flex uk-flex-middle" uk-height-viewport>
    <div class="uk-width-1-1">
      <div class="uk-container">
        <div class="uk-grid-margin uk-grid uk-grid-stack" uk-grid>
          <div class="uk-width-1-1@m">
            <div class="uk-margin uk-width-large uk-margin-auto uk-card uk-card-default uk-card-body uk-box-shadow-large">
              <h2 class="uk-text-center uk-margin-small">Digital ID</h2>
              <h3 class="uk-card-title uk-text-center uk-margin-small">Sign Up</h3>
              <form class="uk-width-1-1" @submit.prevent="login">
                <div class="uk-margin">
                  <label class="uk-form-label"
                    >Account Email <span class="uk-text-danger"><b>*</b></span></label
                  >
                  <div class="uk-inline uk-width-1-1">
                    <span class="uk-form-icon" uk-icon="icon: mail"></span>
                    <input
                      class="uk-input uk-form-large"
                      placeholder="Enter your Email"
                      type="text"
                      v-model="email"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.email }}</div>
                </div>
                <div class="uk-margin">
                  <label class="uk-form-label"
                    >Account Password <span class="uk-text-danger"><b>*</b></span></label
                  >
                  <div class="uk-inline uk-width-1-1">
                    <span class="uk-form-icon" uk-icon="icon: lock"></span>
                    <input
                      class="uk-input uk-form-large"
                      placeholder="********"
                      type="password"
                      v-model="password"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.password }}</div>
                </div>
                <div class="uk-margin">
                  <label class="uk-form-label"
                    >Confirm Account Password
                    <span class="uk-text-danger"><b>*</b></span></label
                  >
                  <div class="uk-inline uk-width-1-1">
                    <span class="uk-form-icon" uk-icon="icon: lock"></span>
                    <input
                      class="uk-input uk-form-large"
                      placeholder="********"
                      type="password"
                      v-model="passwordconfirm"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.passwordconfirm }}</div>
                </div>
                <div class="uk-margin">
                  <button
                    class="uk-button uk-button-primary uk-button-large uk-width-1-1"
                    :disabled="submiting"
                  >
                    Signup
                    <div class="uk-margin-left" v-if="submiting" uk-spinner></div>
                  </button>
                </div>
                <div class="uk-text-small uk-text-center">
                  Registered? <router-link to="/login">Login to an account</router-link>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SignupForm',
  data() {
    return {
      email: '',
      password: '',
      passwordconfirm: '',
      submiting: false,
      error: { email: null, password: null, passwordconfirm: null },
    };
  },
  methods: {
    async login() {
      this.error = { email: null, password: null, passwordconfirm: null };
      this.submiting = true;
      const loginData = {
        email: this.email,
        password: this.password,
        passwordconfirm: this.passwordconfirm,
      };
      const response = await this.$store.dispatch('signUpUserApi', loginData);
      if (response.formError) {
        this.error = response.formError;
      }
      this.submiting = false;
    },
  },
};
</script>

