
import Vue from 'vue';
import api from 'Api';
// import router from '../../../router';

const state = {
  usersLoading: true,
  data: [],
  roles: [],
  status: [],
};

const getters = {
  usersData: state => state.data,
  usersLoading: state => state.usersLoading,
  roleTypes: state => state.roles,
  statusTypes: state => state.status,
};

const actions = {
  async getUsers(context, payload) {
    return await api(false).get('/admin/accounts', {}).then((response) => {
      context.commit('setUsersData', response.data ? response.data : []);
      return response.data;
    })
      .catch(error => error);
  },
  getUsersRoleTypes(context, payload) {
    return api(false).get(`/lookup/account-role`, {}).then(response =>
      state.roles = response.data
    )
      .catch(error => error);
  },
  getUsersStatusTypes(context, payload) {
    return api(false).get(`/lookup/account-status`, {}).then(response =>
      state.status = response.data
    )
      .catch(error => error);
  },
  editUser(context, payload) {
    const params = new URLSearchParams();
    params.append('ID', payload.id);
    params.append('Status', payload.status);
    params.append('Role', payload.role);
    return api(false).put(`/admin/account/${payload.id}`, params).then(async (response) => {
      Vue.$vToastify.success('Updated Successfully');
      await context.dispatch('getUsers');
      document.getElementById('users-edit-modal').click();
      return response;
    })
      .catch((error) => {
        let responseData = error.response;
        responseData = { ...responseData, formError: {} };
        if (error.response.data) {
          responseData.formError.status = responseData.data.Status || null;
          responseData.formError.role = responseData.data.Role || null;
        }
        return responseData;
      });
  },
};

const mutations = {
  setUsersData(state, data) {
    state.data = data;
    state.usersLoading = false;
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
  name: 'Courses',
};
