<template>
  <div
    class="uk-section uk-section-muted uk-flex uk-flex-middle uk-animation-fade"
    uk-height-viewport
  >
    <div class="uk-width-1-1">
      <div class="uk-container">
        <div class="uk-grid-margin uk-grid uk-grid-stack" uk-grid>
          <div class="uk-width-1-1@m">
            <div
              class="uk-margin uk-width-large uk-margin-auto uk-card uk-card-default uk-card-body uk-box-shadow-large"
            >
              <h2 class="uk-card-title uk-text-center uk-margin">Account Settings</h2>
              <form class="uk-width-1-1" @submit.prevent="profileUpdateSubmit">
                <div class="uk-margin">
                  <label class="uk-form-label">Name</label>
                  <div class="uk-inline uk-width-1-1">
                    <input
                      class="uk-input uk-form-large"
                      placeholder="Name"
                      type="text"
                      v-model="formData.name"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.name }}</div>
                </div>
                <div class="uk-margin">
                  <label class="uk-form-label">Email</label>
                  <div class="uk-inline uk-width-1-1">
                    <input
                      class="uk-input uk-form-large"
                      placeholder="Email"
                      type="email"
                      disabled
                      v-model="formData.email"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.email }}</div>
                </div>
                <div class="uk-margin">
                  <label class="uk-form-label"
                    >Phone number <span class="uk-text-danger"><b>*</b></span></label
                  >
                  <div class="uk-inline uk-width-1-1">
                    <input
                      class="uk-input uk-form-large hide-arrows"
                      placeholder="Phone number"
                      type="number"
                      min="1"
                      oninput="if(this.value < 0) this.value = '';"
                      v-model.number="formData.phone"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.phone }}</div>
                </div>
                <vue-dropzone
                  ref="courseImage"
                  id="dropzone"
                  :options="dropzoneOptions"
                  @vdropzone-complete="afterComplete"
                  :useCustomSlot="true"
                >
                  <div class="dropzone-custom-content uk-text-center">
                    <span uk-icon="icon: cloud-upload"></span>
                    <span class="uk-text-middle">Upload image </span>
                    <div>
                      <span class="uk-link">select one</span>
                    </div>
                    <div class="uk-text-danger">{{ error.image }}</div>
                  </div></vue-dropzone
                >

                <div class="uk-margin">
                  <label class="uk-form-label">Country</label>
                  <div class="uk-inline uk-width-1-1">
                    <input
                      class="uk-input uk-form-large"
                      placeholder="Name"
                      type="text"
                      v-model="formData.country"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.name }}</div>
                </div>

                <div class="uk-margin">
                  <label class="uk-form-label">Address</label>
                  <div class="uk-inline uk-width-1-1">
                    <input
                      class="uk-input uk-form-large"
                      placeholder="Name"
                      type="text"
                      v-model="formData.address"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.name }}</div>
                </div>


                <div class="uk-margin">
                  <button
                    class="uk-button uk-button-primary uk-button-large uk-width-1-1"
                    :disabled="submiting"
                  >
                    Submit
                    <div class="uk-margin-left" v-if="submiting" uk-spinner></div>
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import vue2Dropzone from 'vue2-dropzone';
import 'vue2-dropzone/dist/vue2Dropzone.min.css';

export default {
  name: 'ProfileViewAndUpdateAPI',
  components: {
    vueDropzone: vue2Dropzone,
  },
  computed: {
    ...mapGetters(['loginUser']),
  },
  data() {
    return {
      dropzoneOptions: {
        url: 'https://httpbin.org/post',
        thumbnailWidth: 170,
        maxFilesize: 0.5,
        headers: { 'My-Awesome-Header': 'header value' },
        addRemoveLinks: true,
      },
      formData: {
        name: '',
        email: '',
        phone: '',
        country: '',
        address: '',
        image: null,
      },
      error: {
        image: null,
      },
      submiting: false,
    };
  },
  mounted() {
    this.formData.email = this.loginUser.email;
    this.formData.name = this.loginUser.name;
    this.formData.phone = this.loginUser.phone;
    this.formData.country = this.loginUser.country;
    this.formData.address = this.loginUser.address;
  },
  methods: {
    afterComplete(file) {
      this.formData.image = file;
    },
    async profileUpdateSubmit() {
      this.submiting = true;
      const response = await this.$store.dispatch('updateProfile', {
        ...this.formData,
        id: this.loginUser.id,
      });
      if (response.formError) {
        this.error = response.formError;
      }
      this.submiting = false;
    },
  },
};
</script>
<style lang="less" scope>
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
</style>
