                  />
                </div>
                <div class="uk-text-danger">{{ error.account_email }}</div>
              </div>
              <!-- <div class="uk-margin">
              <label class="uk-form-label"
                >Account name
                <span class="uk-text-danger"><b>*</b></span></label
              >
              <div class="uk-inline uk-width-1-1">
                <input
                  class="uk-input uk-form-large"
                  placeholder="Account Name"
                  type="text"
                  v-model="formData.account_name"
                />
              </div>
              <div class="uk-text-danger">{{ error.account_name }}</div>
            </div> -->
              <div class="uk-margin">
                <label class="uk-form-label"
                  >Type <span class="uk-text-danger"><b>*</b></span></label
                >
                <div class="uk-inline uk-width-1-1">
                  <select class="uk-select" v-model="formData.type">
                    <option
                      v-for="(item, index) in CertificateTypes"
                      :key="index"
                      :value="item.id"
                    >
                      {{ item.name }}
                    </option>
                  </select>
                </div>
