                      v-model="formData.activated"
                    />Yes</label
                  >
                  <label style="padding-left: 10px"
                    ><input
                      class="uk-radio"
                      type="radio"
                      value="false"
                      name="activated"
                      v-model="formData.activated"
                    />No</label
                  >
                </div>
                <div class="uk-text-danger">{{ error.activated }}</div>
              </div>
              <div class="uk-margin">
                <label class="uk-form-label">Verified</label>
                <div class="uk-inline uk-width-1-1">
                  <label style="padding-left: 10px"
                    ><input
                      class="uk-radio"
                      type="radio"
                      name="verified"
                      value="true"
                      v-model="formData.verified"
                    />Yes</label
                  >
                  <label style="padding-left: 10px"
                    ><input
                      class="uk-radio"
