</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'CreateCertificateModal',
  computed: {
    ...mapGetters(['CertificateTypes', 'loginUser']),
    courseID() {
      return this.courseid;
    },
  },
  mounted() {
    this.$store.dispatch('getCertificatesTypes');
  },
  props: ['courseid'],
  data() {
    return {
      formData: {
        fk_course_id: this.courseID,
        account_email: '',
        type: 1,
        issued: this.$moment().format('YYYY-MM-DD'),
        activated: true,
        kin: '',
        passport: '',
        rank: '',
        date_of_birth: this.$moment().format('YYYY-MM-DD'),
        licence: '',
        ship_experience: '',
        experience: '',
        nationality: '',
        verified: false,
        course_id: '',
        account_name: '',
      },
      error: {
        fk_course_id: null,
        account_email: '',
        type: '',
        issued: '',
        activated: null,
        kin: '',
        passport: '',
        rank: '',
        date_of_birth: '',
        licence: '',
        ship_experience: '',
        experience: '',
        nationality: '',
        verified: null,
        course_id: '',
        account_name: '',
      },
      submiting: false,
      formMode: 'create',
    };
  },
  methods: {
    async createSubmit() {
      this.submiting = true;
      if (this.formMode === 'create') {
        this.formData = {
          ...this.formData,
          issued: this.$moment(this.formData.issued).toISOString(true),
          date_of_birth: this.$moment(this.formData.date_of_birth).toISOString(true),
          fk_course_id: this.courseID,
        };
        const response = await this.$store.dispatch('createCertificate', {
          payload: this.formData,
          resetform: this.resetFullFormandCloseModal,
        });
        if (response.formError) {
          this.error = response.formError;
        }
        this.submiting = false;
      } else {
        const response = await this.$store.dispatch('editCourse', this.formData);
        if (response.formError) {
          this.error = response.formError;
        }
      }
      this.submiting = false;
    },
    setEditData(item) {
      this.resetFromError();
      this.formData = { ...this.formData, ...item };
      this.formMode = 'edit';
    },
    resetform() {
      this.formData = {
        fk_course_id: this.courseID,
        account_email: '',
        type: 1,
        issued: this.$moment().format('YYYY-MM-DD'),
        activated: true,
        kin: '',
        passport: '',
        rank: '',
        date_of_birth: this.$moment().format('YYYY-MM-DD'),
        licence: '',
        ship_experience: '',
        experience: '',
        nationality: '',
        verified: false,
        course_id: '',
        account_name: '',
      };
    },
    setCreateData() {
      this.resetFromError();
      this.resetform();
      this.formMode = 'create';
    },
    resetFullFormandCloseModal() {
      this.resetform();
      this.resetFromError();
    },
    resetFromError() {
      this.error = {
        fk_course_id: '',
        account_email: '',
        type: '',
        issued: '',
        activated: null,
        kin: '',
        passport: '',
        rank: '',
        date_of_birth: '',
        licence: '',
        ship_experience: '',
        experience: '',
        nationality: '',
        verified: '',
        course_id: '',
        account_name: '',
      };
    },
  },
};
</script>

