/* eslint-disable no-return-assign */
/* eslint-disable no-param-reassign */
/* eslint-disable no-shadow */

import Vue from 'vue';
import api from 'Api';
// import router from '../../../router';

const state = {
  coursesLoading: true,
  CourseCategories: [],
  data: [],
};

const getters = {
  coursesData: state => state.data,
  courseCategories: state => state.CourseCategories,
  isCourseLoading: state => state.coursesLoading,
};

const actions = {
  async getCoursesApi(context, payload) {
    // eslint-disable-next-line no-return-await
    return await api(false).get(`/courses/${payload}`, {}).then((response) => {
      context.commit('setCoursesData', response.data ? response.data : []);
      return response.data;
    })
      .catch(error => error);
  },
  async GetCourseCategories() {
    return api(false).get('/lookup/course-categories', {}).then(response => state.CourseCategories = response.data)
      .catch(error => error);
  },
  createCourse(context, payload) {
    const params = new FormData();
    // const params = new URLSearchParams();
    params.append('CourseName', payload.course_name);
    params.append('CourseCategory', payload.course_category);
    params.append('CertificateName', payload.certificate_name);
    params.append('Description', payload.description);
    params.append('AdditionalDescription', payload.additional_description);
    params.append('Expire', payload.expire);
    params.append('Image', payload.image);
    return api(false).post('/course', params).then(async (response) => {
      Vue.$vToastify.success('Created Successfully');
      await context.dispatch('getCoursesApi', payload.userID);
      document.getElementById('course-modal').click();
      // eslint-disable-next-line no-undef
      UIkit.modal('#course-modal').hide();
      return response.data;
    })
      .catch((error) => {
        let responseData = error.response;
        // if (error.response.status === 401) {
        responseData = { ...responseData, formError: {} };
        if (error.response.data) {
          responseData.formError.course_name = responseData.data.CourseName || null;
          responseData.formError.course_category = responseData.data.course_category || null;
          responseData.formError.certificate_name = responseData.data.CertificateName || null;
          responseData.formError.description = responseData.data.Description || null;
          // eslint-disable-next-line max-len
          responseData.formError.additional_description = responseData.data.AdditionalDescription || null;
          responseData.formError.expire = responseData.data.Expire || null;
        }
        responseData.formError.image = payload.image ? '' : 'Required';

        // }
        if (error.response.data.system) {
          Vue.$vToastify.error(error.response.data.system);
          state.publicCertErrorMessage = error.response.data.system;
        }
        if (error.response.data.error) {
          Vue.$vToastify.error(error.response.data.error);
          state.publicCertErrorMessage = error.response.data.error;
        }
        return responseData;
      });
  },
  editCourse(context, payload) {
    const params = new FormData();
    // const params = new URLSearchParams();
    // params.append('ID', payload.id);
    params.append('CourseName', payload.course_name);
    params.append('CourseCategory', payload.course_category);
    params.append('CertificateName', payload.certificate_name);
    params.append('Description', payload.description);
    params.append('AdditionalDescription', payload.additional_description);
    params.append('Expire', payload.expire);
    params.append('Image', payload.image);
    return api(false).put(`/course/${payload.id}`, params).then(async (response) => {
      Vue.$vToastify.success('Updated Successfully');
      await context.dispatch('getCoursesApi', payload.userID);
      document.getElementById('course-modal').click();
      // eslint-disable-next-line no-undef
      UIkit.modal('#course-modal').hide();
      return response;
    })
      .catch((error) => {
        let responseData = error.response;
        responseData = { ...responseData, formError: {} };
        if (error.response.data) {
          responseData.formError.certificate_name = responseData.data.CertificateName || null;
          responseData.formError.course_name = responseData.data.CourseName || null;
          responseData.formError.course_category = responseData.data.CourseCategory || null;
          responseData.formError.description = responseData.data.Description || null;
          // eslint-disable-next-line max-len
          responseData.formError.additional_description = responseData.data.AdditionalDescription || null;
          responseData.formError.expire = responseData.data.Expire || null;
        }
        responseData.formError.image = payload.image ? '' : 'Required';
        if (error.response.data.system) {
          Vue.$vToastify.error(error.response.data.system);
          state.publicCertErrorMessage = error.response.data.system;
        }
        if (error.response.data.error) {
          Vue.$vToastify.error(error.response.data.error);
          state.publicCertErrorMessage = error.response.data.error;
        }
        return responseData;
      });
  },
  deleteCourse(context, payload) {
    return api(false).delete(`/course/${payload.id}`).then(async (response) => {
      Vue.$vToastify.success('Deleted Successfully');
      await context.dispatch('getCoursesApi', payload.userid);
      return response.data;
    })
      .catch((error) => {
        if (error.response.data.system) {
          Vue.$vToastify.error(error.response.data.system);
          state.publicCertErrorMessage = error.response.data.system;
        }
        if (error.response.data.error) {
          Vue.$vToastify.error(error.response.data.error);
          state.publicCertErrorMessage = error.response.data.error;
        }
      });
  },
};

const mutations = {
  setCoursesData(state, data) {
    state.data = data;
    state.coursesLoading = false;
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
  name: 'Courses',
};
