import Vue from 'vue';
import Router from 'vue-router';
// pages
import Home from './views/Home.vue';
import Login from './views/Login.vue';
import Signup from './views/Signup.vue';
import PageNotFound from './views/PageNotFound.vue';
import Certificates from './views/Certificates.vue';
import Courses from './views/Courses.vue';
import Users from './views/Users.vue';
import AdminUserCourseList from './views/AdminUserCourseList.vue';
import AdminUserCourseCertificateList from './views/AdminUserCourseCertificateList.vue';
import PublicCerificate from './views/PublicCerificate.vue';
import Wallet from './views/Wallet.vue';
import Setting from './views/Setting.vue';
import Landing from './views/Landing.vue';
// import About from './views/About.vue';
import VerifyEmail from './views/Verify.vue';
// layouts
import UserLayout from './layouts/UserLayout.vue';
import PublicCertificateLayout from './layouts/PublicCertificateLayout.vue';
import LandingLayout from './layouts/LandingLayout.vue';
// helpers
import AuthService from './helpers/auth';
import store from './store/store';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'landing',
      component: Landing,
      meta: {
        requiresAuth: false,
        title: 'Landing',
        layout: LandingLayout,
        restrictForFoginUser: true,
        forRol: [],
      },
    },
    {
      path: '/dashboard',
      name: 'home',
      component: Home,
      meta: {
        requiresAuth: true,
        title: 'Home',
        layout: UserLayout,
        forRol: [1, 2, 3, 4],
      },
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        restrictForFoginUser: true,
      },
    },
    {
      path: '/signup',
      name: 'signup',
      component: Signup,
      meta: {
        restrictForFoginUser: true,
      },
    },
    {
      path: '/certificates/:id',
      name: 'certificates',
      component: Certificates,
      meta: {
        requiresAuth: true,
        title: 'Certificates',
        layout: UserLayout,
        forRol: [2, 3],
      },
    },
    {
      path: '/courses',
      name: 'courses',
      component: Courses,
      meta: {
        requiresAuth: true,
        title: 'Courses',
        layout: UserLayout,
        forRol: [3],
      },
    },
    {
      path: '/setting',
      name: 'setting',
      component: Setting,
      meta: {
        requiresAuth: true,
        title: 'Setting',
        layout: UserLayout,
        forRol: [2, 3, 4],
      },
    },
    {
      path: '/accounts',
      name: 'accounts',
      component: Users,
      meta: {
        requiresAuth: true,
        title: 'Accounts',
        layout: UserLayout,
        forRol: [4],
      },
    },
    {
      path: '/wallet',
      name: 'wallet',
      component: Wallet,
      meta: {
        requiresAuth: true,
        title: 'User',
        layout: UserLayout,
        forRol: [2],
      },
    },
    // {
    //   path: '/about',
    //   name: 'about',
    //   component: About,
    //   meta: {
    //     requiresAuth: false,
    //     title: 'Landing',
    //     layout: LandingLayout,
    //     restrictForFoginUser: true,
    //     forRol: [],
    //   },
    // },
    {
      path: '/user/:uid/course',
      name: 'userCourses',
      component: AdminUserCourseList,
      meta: {
        requiresAuth: true,
        title: 'User',
        layout: UserLayout,
        forRol: [4],
      },
    },
    {
      path: '/user/:uid/course/:cid/certificates',
      name: 'userCourseCertificates',
      component: AdminUserCourseCertificateList,
      meta: {
        requiresAuth: true,
        title: 'User',
        layout: UserLayout,
        forRol: [4],
      },
    },
    {
      path: '/view/certificate/:id',
      name: 'certificate',
      component: PublicCerificate,
      meta: {
        requiresAuth: false,
        title: 'User',
        layout: PublicCertificateLayout,
        forRol: [],
      },
    },
    {
      path: '/verify/:id',
      name: 'emailVerify',
      component: VerifyEmail,
      meta: {
        requiresAuth: false,
        title: 'emailVerify',
        forRol: [],
      },
    },
    {
      path: '/about',
      name: 'about',
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue'),
    },
    {
      path: '*',
      name: 'PageNotFound',
      component: PageNotFound,
    },
  ],
});

router.beforeEach(async (to, from, next) => {
  const auth = new AuthService();
  const authenticated = await auth.authenticated;
  const cookieToken = Vue.$cookies.get('access_token');

  const loggedIn = authenticated;
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!loggedIn) {
      next({
        path: '/',
      });
    } else {
      const loginUser = store.state.auth.user;
      // eslint-disable-next-line max-len
      if (to.meta.forRol && loginUser && loginUser.role && to.meta.forRol.includes(loginUser.role)) {
        next();
      } else {
        next({
          path: '/dashboard',
        });
      }
    }
  } else {
    next();
  }
  if (to.matched.some(record => record.meta.restrictForFoginUser)) {
    if (cookieToken === null && !loggedIn) {
      next();
    } else if (store.state.auth.user.role === 2) {
      next({
        path: '/wallet',
      });
    } else if ((store.state.auth.user.role === 3)) {
      next({
        path: '/courses',
      });
    } else if ((store.state.auth.user.role === 4)) {
      next({
        path: '/accounts',
      });
    } else {
      next();
    }
  }
});

export default router;
