      <!-- This is a button toggling the modal -->

      <!-- This is the modal -->
      <div id="course-modal" uk-modal>
        <div class="uk-modal-dialog uk-modal-body uk-box-shadow-large">
          <div class="uk-grid-margin uk-grid uk-grid-stack" uk-grid>
            <div class="uk-width-1-1@m">
              <h3 class="uk-card-title uk-text-center uk-margin-small">
                {{ formMode === "create" ? "Create" : "Edit" }}
              </h3>
              <form class="uk-width-1-1" @submit.prevent="createSubmit">
                <div class="uk-margin">
                  <label class="uk-form-label"
                    >Certificate Name <span class="uk-text-danger"><b>*</b></span></label
                  >
                  <div class="uk-inline uk-width-1-1">
                    <input
                      class="uk-input uk-form-large"
                      placeholder="Name"
                      type="text"
                      v-model="formData.certificate_name"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.certificate_name }}</div>
                </div>
                <div class="js-upload uk-placeholder uk-text-center">
                  <span uk-icon="icon: cloud-upload"></span>
                  <span class="uk-text-middle">Upload Logo or </span>
