                  <label class="uk-form-label"
                    >Expire <span class="uk-text-danger"><b>*</b></span></label
                  >
                  <div class="uk-inline uk-width-1-1">
                    <input
                      class="uk-input uk-form-large"
                      placeholder="In Months"
                      type="number"
                      min="1"
                      oninput="if(this.value < 0) this.value = '';"
                      v-model.number="formData.expire"
                    />
                  </div>
                  <div class="uk-text-danger">{{ error.expire }}</div>
                </div>

                <div class="uk-margin">
                  <label class="uk-form-label"
                    >Course ID <span class="uk-text-danger"><b>*</b></span></label
