                  <div class="uk-text-danger">{{ error.country }}</div>
                </div>

                <div class="uk-margin">
                  <button
                    class="uk-button uk-button-primary uk-button-large uk-width-1-1"
                    :disabled="submiting"
                  >
                    Submit
                    <div class="uk-margin-left" v-if="submiting" uk-spinner></div>
                  </button>
                </div>
