    </article>
    <CreateCertificateModal :courseid="createCertificateSelected" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import CreateCertificateModal from '../components/CreateCertificateModal.vue';

export default {
  name: 'CoursesListing',
  components: {
    CreateCertificateModal,
  },
  computed: {
    ...mapGetters(['coursesData', 'isCourseLoading', 'loginUser']),
  },
  mounted() {
    this.$store.dispatch('getCoursesApi', this.$route.params.uid);
  },
  data() {
    return {
      headers: [
        {
          text: 'Dessert (100g serving)',
          align: 'start',
          sortable: false,
          value: 'name',
        },
        { text: 'Calories', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: 'Iron (%)', value: 'iron' },
      ],
      desserts: [
        {
          name: 'Frozen Yogurt',
          calories: 159,
          fat: 6.0,
          carbs: 24,
          protein: 4.0,
          iron: '1%',
        },
        {
          name: 'Ice cream sandwich',
          calories: 237,
          fat: 9.0,
          carbs: 37,
          protein: 4.3,
          iron: '1%',
        },
        {
          name: 'Eclair',
          calories: 262,
          fat: 16.0,
          carbs: 23,
          protein: 6.0,
          iron: '7%',
        },
        {
          name: 'Cupcake',
          calories: 305,
          fat: 3.7,
          carbs: 67,
          protein: 4.3,
          iron: '8%',
        },
        {
          name: 'Gingerbread',
          calories: 356,
          fat: 16.0,
          carbs: 49,
          protein: 3.9,
          iron: '16%',
        },
        {
          name: 'Jelly bean',
          calories: 375,
          fat: 0.0,
          carbs: 94,
          protein: 0.0,
          iron: '0%',
        },
        {
          name: 'Lollipop',
          calories: 392,
          fat: 0.2,
          carbs: 98,
          protein: 0,
          iron: '2%',
        },
        {
          name: 'Honeycomb',
          calories: 408,
          fat: 3.2,
          carbs: 87,
          protein: 6.5,
          iron: '45%',
        },
        {
          name: 'Donut',
          calories: 452,
          fat: 25.0,
          carbs: 51,
          protein: 4.9,
          iron: '22%',
        },
        {
          name: 'KitKat',
          calories: 518,
          fat: 26.0,
          carbs: 65,
          protein: 7,
          iron: '6%',
        },
      ],
      createCertificateSelected: null,
      formData: {
        certificate_name: '',
        description: '',
        course_name: '',
        additional_description: '',
        expire: '',
        country: '',
        image: null,
      },
      error: {
        certificate_name: '',
        description: '',
        course_name: '',
        additional_description: '',
        expire: '',
        course_id: '',
        country: '',
        image: null,
      },
      submiting: false,
      formMode: 'create',
    };
  },
  methods: {
    onFileChange(e) {
      const files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      // eslint-disable-next-line prefer-destructuring
      this.formData.image = files[0];
    },
    async createSubmit() {
      this.submiting = true;
      if (this.formMode === 'create') {
        const response = await this.$store.dispatch('createCourse', {
          ...this.formData,
          userID: this.$route.params.uid,
        });
        if (response.formError) {
          this.error = response.formError;
        }
      } else {
        const response = await this.$store.dispatch('editCourse', {
          ...this.formData,
          userID: this.$route.params.uid,
        });
        if (response.formError) {
          this.error = response.formError;
        }
      }
      this.submiting = false;
    },
    setEditData(item) {
      this.resetFromError();
      this.formData = { ...this.formData, ...item };
      this.formMode = 'edit';
    },
    setCreateData() {
      this.resetFromError();
      this.formData = {
        certificate_name: '',
        description: '',
        course_name: '',
        additional_description: '',
        expire: '',
        course_id: '',
        image: null,
        country: '',
      };
      this.formMode = 'create';
    },
    deleteCourse(item) {
      this.$swal({
        title: 'Delete this course?',
        text: 'This action cannot be undone, please confirm to continue',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        confirmButtonText: 'Confirm',
      }).then((result) => {
        if (result.value) {
          this.$store.dispatch('deleteCourse', item);
        }
      });
    },
    resetFromError() {
      this.error = {
        certificate_name: '',
        description: '',
        course_name: '',
        additional_description: '',
        expire: '',
        course_id: '',
        image: null,
        country: '',
      };
    },
  },
};
</script>

