import Vue from 'vue';
import api from 'Api';
import router from '../../../router';

const state = {
  user: null,
  loading: false,
  formData: {
    email: '',
  },
};

const getters = {
  loginUser: state => state.user,
  dashboardFormData: state => state.formData,
};

const actions = {
  // getDashboardApi() {
  //   const params = new URLSearchParams();
  //   params.append('id', 1);
  //   params.append('name', 'user.email');
  //   params.append('description', '');
  //   params.append('course_name', '');
  //   params.append('additional_description', '');
  //   params.append('expire', 2);
  //   params.append('course_id', 444);
  //   return api(false).get('/course/1', params).then((response) => {
  //     state.formData = response.data.data;
  //     return response.data.data;
  //   })
  //     .catch(error => error);
  // },
  signUpUserApi(context, payload) {
    const user = payload;
    const params = new URLSearchParams();
    params.append('email', user.email);
    params.append('password', user.password);
    params.append('passwordconfirm', user.passwordconfirm);
    return api(false).post('/signup', params).then(() => {
      Vue.$vToastify.success(`Validation email has been sent to: ${user.email}`);
      router.push({ path: '/login' });
    })
      .catch((error) => {
        let responseData = error.response;
        responseData = { ...responseData, formError: {} };
        if (error.response.data) {
          responseData.formError.email = error.response.data.Email || null;
          responseData.formError.password = error.response.data.Password || null;
          responseData.formError.passwordconfirm = error.response.data.PasswordConfirm || null;
          if (error.response.data.password) {
            responseData.formError.password = error.response.data.password || null;
          }
          if (error.response.data.email) {
            responseData.formError.email = error.response.data.email || null;
          }
        }
        return responseData;
      });
  },
  signInUserApi(context, payload) {
    const user = payload;
    state.loading = true;
    const params = new URLSearchParams();
    params.append('email', user.email);
    params.append('password', user.password);
    return api(false).post('/signin', params, { withCredentials: true }).then(async (response) => {
      if (response.data && response.data) {
        const cookieExpireTime = Number(process.env.VUE_APP_COOKIE_EXPIRER_DAY);
        const todaysDate = new Date();
        todaysDate.setDate(todaysDate.getDate() + cookieExpireTime);
        Vue.$cookies.set('access_token', { token: response.data, refreshed_at: new Date() }, todaysDate);
        await api(false).get('/account').then(async (response) => {
          const userData = response.data;
          await context.commit('setUserInStore', userData);

          router.push({ path: '/' });
        }).catch((error) => {
          context.commit('apiError', error.response.data);
        });
      }
      return {};
    })
      .catch((error) => {
        let responseData = error.response;
        responseData = { ...responseData, formError: {} };
        if (error.response.data) {
          responseData.formError.email = responseData.data.Email || null;
          responseData.formError.password = responseData.data.Password || null;
        }
        return responseData;
      });
  },
  updateProfile(context, payload) {
    const params = new FormData();
    params.append('Name', payload.name);
    params.append('Email', payload.email);
    params.append('Phone', payload.phone);
    params.append('Image', payload.image);
    params.append('Country', payload.country);
    params.append('Address', payload.address);
    return api(false).put(`/account/${payload.id}`, params, { withCredentials: true }).then(async (response) => {
      Vue.$vToastify.success('Account Detail Updated');
      return {};
    })
      .catch((error) => {
        let responseData = error.response;
        responseData = { ...responseData, formError: {} };
        responseData.formError.image = payload.image ? '' : 'Required';
        return responseData;
      });
  },
  verifyEmail(context, payload) {
    return api(false).post(`/signup/${payload}`).then(async (response) => {
      Vue.$vToastify.success('Email verified Successfully');
      router.push({ path: '/login' });
      return response.data;
    })
      .catch((error) => {
        if (error.response.data.system) {
          Vue.$vToastify.error(error.response.data.system);
        }
        if (error.response.data.error) {
          Vue.$vToastify.error(error.response.data.error);
        }
        router.push({ path: '/' });
      });
  },
  async getUserProfile(context) {
    await api(false).get('/account').then((response) => {
      const userData = response.data;
      context.commit('setUserInStore', userData);
      return response;
    }).catch((error) => {
      context.commit('logoutUser');
      router.push({ path: '/' });
      return error;
    });
  },
  logoutUser(context) {
    context.commit('logoutUser');
    router.push({ path: '/' });
  },
};

const mutations = {
  setUserInStore(state, user) {
    state.user = user;
  },
  apiError(state, error) {
    Vue.notify({
      group: 'messages',
      type: 'error',
      duration: 5000,
      text: error,
    });
  },
  logoutUser(state) {
    state.user = null,
      Vue.$cookies.remove('access_token');
  },
  profileSuccess() {
    Vue.notify({
      group: 'messages',
      type: 'success',
      text: 'Profile Updated',
    });
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
  name: 'AuthAuth',
};
