/* eslint-disable no-undef */
/* eslint-disable no-unused-expressions */
/* eslint-disable no-return-await */
/* eslint-disable no-shadow */

import Vue from 'vue';
import api from 'Api';
// import router from '../../../router';

const state = {
  loading: true,
  data: [],
  CertificateTypes: [],
  publicCertificateData: null,
  publicCertificateLoading: true,
  publicCertErrorMessage: null,
};

const getters = {
  Certificatedata: state => state.data,
  isLoading: state => state.loading,
  CertificateTypes: state => state.CertificateTypes,
  publicCertificate: state => state.publicCertificateData,
  publicCertificateLoading: state => state.publicCertificateLoading,
  publicCertErrorMessage: state => state.publicCertErrorMessage,
};

const actions = {
  getCertificatesApi(context, payload) {
    state.loading = true;
    return api(false).get(`/certificates/course/${payload}`, {}).then(response =>
      context.commit('setCertificatesData', response.data ? response.data : []))
      .catch(error => error);
  },
  getPUblicCertificateDetail(context, payload) {
    state.publicCertificateData = null;
    state.publicCertificateLoading = true;
    state.publicCertErrorMessage = null;
    return api(false).get(`/view/certificate/${payload}`, {}).then(response =>
      context.commit('setPublicCertificatesData', response.data ? response.data : null)).catch((error) => {
      if (error.response.data.system) {
        Vue.$vToastify.error(error.response.data.system);
        state.publicCertErrorMessage = error.response.data.system;
      }
      if (error.response.data.error) {
        Vue.$vToastify.error(error.response.data.error);
        state.publicCertErrorMessage = error.response.data.error;
      }
      state.publicCertificateLoading = false;
    });
  },
  getCertificatesTypes() {
    // eslint-disable-next-line no-return-assign
    return api(false).get('/lookup/certificate-type', {}).then(response =>
      state.CertificateTypes = response.data)
      .catch(error => error);
  },
  async createCertificate(context, { payload, resetform }) {
    // const params = new FormData();
    const params = new URLSearchParams();
    // params.append('CourseID', payload.fk_course_id);
    params.append('AccountEmail', payload.account_email);
    params.append('Type', payload.type);
    params.append('Issued', payload.issued);
    params.append('Activated', payload.activated);
    params.append('Kin', payload.kin);
    params.append('Rank', payload.rank);
    params.append('DateOfBirth', payload.date_of_birth);
    params.append('Licence', payload.licence);
    params.append('ShipExpereince', payload.ship_experience);
    params.append('Nationality', payload.nationality);
    params.append('Verified', payload.verified);

    return await api(false).post(`/certificate/${payload.fk_course_id}`, params).then(async (response) => {
      Vue.$vToastify.success('Created Successfully');
      document.getElementById('certificate-modal').click();
      UIkit.modal('#certificate-modal').hide();
      await context.dispatch('getCertificatesApi', payload.fk_course_id);
      resetform;
      return response.data;
    })
      .catch((error) => {
        let responseData = error.response;
        // if (error.response.status === 401) {
        responseData = { ...responseData, formError: {} };
        if (error.response.data) {
          responseData.formError.type = responseData.data.Type || null;
          responseData.formError.issued = responseData.data.Issued || null;
          responseData.formError.activated = responseData.data.Activated || null;
          responseData.formError.acessed = responseData.data.Accessed || null;
          responseData.formError.account_email = responseData.data.AccountEmail || null;
          responseData.formError.kin = responseData.data.Kin || null;
          responseData.formError.rank = responseData.data.Rank || null;
          responseData.formError.date_of_birth = responseData.data.DateOfBirth || null;
          responseData.formError.licence = responseData.data.Licence || null;
          responseData.formError.ship_experience = responseData.data.ShipExpereince || null;
          responseData.formError.nationality = responseData.data.Nationality || null;
          responseData.formError.verified = responseData.data.Verified || null;
        }
        // }
        return responseData;
      });
  },
  async editCertificate(context, { payload, resetform }) {
    // const params = new FormData();
    const params = new URLSearchParams();
    // params.append('CourseID', payload.fk_course_id);
    // params.append('ID', payload.id);
    params.append('AccountEmail', payload.account_email);
    params.append('Type', payload.type);
    // params.append('Accessed', payload.accessed);
    // params.append('AccountID', payload.fk_account_id);
    params.append('Completed', payload.completed);
    // params.append('CourseID', payload.fk_course_id);
    params.append('Experience', payload.experience);
    params.append('Passport', payload.passport);
    params.append('Issued', payload.issued);
    params.append('Activated', payload.activated);
    params.append('Kin', payload.kin);
    params.append('Rank', payload.rank);
    params.append('DateOfBirth', payload.date_of_birth);
    params.append('Licence', payload.licence);
    params.append('ShipExpereince', payload.ship_experience);
    params.append('Nationality', payload.nationality);
    params.append('Verified', payload.verified);

    return await api(false).put(`/certificate/${payload.id}`, params).then(async (response) => {
      Vue.$vToastify.success('Updated Successfully');
      resetform();
      UIkit.modal('#certificate-edit-modal').hide();
      await context.dispatch('getCertificatesApi', payload.fk_course_id);
      return response.data;
    })
      .catch((error) => {
        let responseData = error.response;
        // if (error.response.status === 401) {
        responseData = { ...responseData, formError: {} };
        if (error.response.data) {
          responseData.formError.type = responseData.data.Type || null;
          responseData.formError.issued = responseData.data.Issued || null;
          responseData.formError.activated = responseData.data.Activated || null;
          responseData.formError.acessed = responseData.data.Accessed || null;
          responseData.formError.account_email = responseData.data.AccountEmail || null;
          responseData.formError.kin = responseData.data.Kin || null;
          responseData.formError.rank = responseData.data.Rank || null;
          responseData.formError.date_of_birth = responseData.data.DateOfBirth || null;
          responseData.formError.licence = responseData.data.Licence || null;
          responseData.formError.ship_experience = responseData.data.ShipExpereince || null;
          responseData.formError.nationality = responseData.data.Nationality || null;
          responseData.formError.verified = responseData.data.Verified || null;
          responseData.formError.passport = responseData.data.Passport || null;
          responseData.formError.accessed = responseData.data.Accessed || null;
          responseData.formError.completed = responseData.data.Completed || null;
        }
        if (error.response.data.system) {
          Vue.$vToastify.error(error.response.data.system);
        }
        if (error.response.data.error) {
          Vue.$vToastify.error(error.response.data.error);
        }
        // }
        return responseData;
      });
  },
  deleteCertificate(context, payload) {
    return api(false).delete(`/certificate/${payload.Certificate.ID}`).then(async (response) => {
      Vue.$vToastify.success('Deleted Successfully');
      await context.dispatch('getCertificatesApi', payload.courseId);
      return response.data;
    })
      .catch((error) => {
        if (error.response.data.system) {
          Vue.$vToastify.error(error.response.data.system);
        }
      });
  },
};

const mutations = {
  setCertificatesData(state, data) {
    state.data = data || [];
    state.loading = false;
  },
  setPublicCertificatesData(state, data) {
    state.publicCertificateData = data;
    state.publicCertificateLoading = false;
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
  name: 'Certificates',
};
