<script>
import vue2Dropzone from 'vue2-dropzone';
import 'vue2-dropzone/dist/vue2Dropzone.min.css';
import { mapGetters } from 'vuex';
import CreateCertificateModal from './CreateCertificateModal.vue';

export default {
  name: 'CoursesListing',
  components: {
    CreateCertificateModal,
    vueDropzone: vue2Dropzone,
  },
  computed: {
    ...mapGetters(['coursesData', 'courseCategories', 'isCourseLoading', 'loginUser']),
  },
  mounted() {
    this.$store.dispatch('getCoursesApi', this.loginUser.id);
    this.$store.dispatch('GetCourseCategories');
  },
  data() {
    return {
      dropzoneOptions: {
        url: 'https://httpbin.org/post',
        thumbnailWidth: 170,
        maxFilesize: 0.5,
        headers: { 'My-Awesome-Header': 'header value' },
        addRemoveLinks: true,
      },
      createCertificateSelected: null,
      formData: {
        certificate_name: '',
        description: '',
        course_name: '',
        course_category: '',
        additional_description: '',
        expire: '',
        image: null,
      },
      error: {
        certificate_name: '',
        description: '',
        course_name: '',
        course_category: '',
        additional_description: '',
        expire: '',
        image: null,
      },
      submiting: false,
      formMode: 'create',
    };
  },
  methods: {
    afterComplete(file) {
      this.formData.image = file;
    },
    async createSubmit() {
      this.submiting = true;
      if (this.formMode === 'create') {
        const response = await this.$store.dispatch('createCourse', {
          ...this.formData,
          userID: this.loginUser.id,
        });
        if (response.formError) {
          this.error = response.formError;
        }
      } else {
        const response = await this.$store.dispatch('editCourse', {
          ...this.formData,
          userID: this.loginUser.id,
        });
        if (response.formError) {
          this.error = response.formError;
        }
      }
      this.submiting = false;
    },
    setEditData(item) {
      this.resetFromError();
      this.formData = { ...this.formData, ...item, image: null };
      this.formMode = 'edit';
    },
    setCreateData() {
      this.resetFromError();
      this.formData = {
        certificate_name: '',
        description: '',
        course_name: '',
        course_category: '',
        additional_description: '',
        expire: '',
        image: null,
      };
      this.formMode = 'create';
    },
    deleteCourse(item) {
      this.$swal({
        title: 'Delete this course?',
        text: 'This action cannot be undone, please confirm to continue',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        confirmButtonText: 'Confirm',
        item,
      }).then((result) => {
        if (result.value) {
          this.$store.dispatch('deleteCourse', {
            ...item,
            userid: this.loginUser.id,
          });
        }
      });
    },
    resetFromError() {
      this.$refs.courseImage.removeAllFiles();
      this.error = {
        certificate_name: '',
        description: '',
        course_name: '',
        course_category: '',
        additional_description: '',
        expire: '',
        image: null,
      };
    },
  },
};
</script>

